import { Fragment } from "react";
import { Menu, Transition } from "@headlessui/react";
import { useState } from "react";
// import { ChevronDownIcon } from '@heroicons/react/20/solid'

export default function DropDown({ miners, handleExisting }) {
  const [drop, setDrop] = useState(false);
  const handleClick = (miner) => {
    setDrop(false);
    handleExisting(miner);
    // active = false;
  };
  return (
    <Menu as="div" className="relative inline-block text-left">
      <div>
        <Menu.Button
          onClick={() => setDrop(!drop)}
          className="inline-flex w-full justify-center gap-x-1.5 rounded-md bg-pink-600 px-3 py-2 text-sm font-semibold text-white shadow-sm ring-1 ring-inset ring-pink-300 hover:bg-pink-700"
        >
          Existing Recipient
          {/* <ChevronDownIcon className="-mr-1 h-5 w-5 text-gray-400" aria-hidden="true" /> */}
        </Menu.Button>
      </div>

      <Transition
        as={Fragment}
        enter="transition ease-out duration-100"
        enterFrom="transform opacity-0 scale-95"
        enterTo="transform opacity-100 scale-100"
        leave="transition ease-in duration-75"
        leaveFrom="transform opacity-100 scale-100"
        leaveTo="transform opacity-0 scale-95"
      >
        <Menu.Items className="absolute right-0 z-10 mt-2 w-56 origin-top-right rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
          <div className="py-1">
            <Menu.Item>
              {
                !drop ? <></> :
                <>
                  {miners.map((miner, index) => {
                    return (
                      <button
                        key={index}
                        onClick={() => handleClick(miner)}
                        className={
                          "text-gray-700 block w-full px-4 py-2 text-left text-sm"
                        }
                      >
                        {miner}
                      </button>
                    );
                  })}
                </>
              }
            </Menu.Item>
          </div>
        </Menu.Items>
      </Transition>
    </Menu>
  );
}
